.hero {
  justify-content: center;
  align-items: center;
  height: 75vh;
  margin-bottom: 20vh;
  display: flex;
  position: relative;
}

.hero .titles {
  color: #0e0d17;
  flex-direction: column;
  gap: 2.8vw;
  display: flex;
}

.hero .titles h1 {
  opacity: .6;
  text-transform: uppercase;
  letter-spacing: .1vw;
  font-size: 2.6vw;
  font-weight: 500;
  line-height: 110%;
}

.hero .titles h2 {
  letter-spacing: -.7vw;
  font-size: 8.8vw;
  font-weight: 500;
  line-height: 110%;
}

.hero .titles h3 {
  opacity: .6;
  letter-spacing: -.07vw;
  padding-right: 24px;
  font-size: 3.7vw;
  font-weight: 400;
  line-height: 140%;
}

.hero .buttons {
  z-index: 3;
  justify-content: space-between;
  width: 100%;
  padding: 6px 12px;
  transition: opacity .5s ease-in-out;
  display: flex;
  position: fixed;
  bottom: 0;
  left: 0;
}

.hero .buttons .button-fill-hover {
  letter-spacing: 0;
  border-radius: 3.6vw;
  gap: 3vw;
  padding: 3vw 6vw;
  font-size: 4.2vw;
  line-height: 100%;
}

.hero .buttons .button-fill-hover .text, .hero .buttons .button-fill-hover .dot {
  z-index: 4;
}

.hero .buttons .button-fill-hover:after {
  z-index: 3;
}

.hero .buttons .btn-additional {
  color: #fff;
  background-color: #0e0d17;
}

.hero .buttons .btn-additional .dot {
  color: #653cf4;
}

.hero .buttons .btn-additional:after {
  background: #653cf4;
}

.hero .buttons .btn-additional:hover .dot {
  color: #0e0d17;
}

.hero .buttons .btn-contact {
  color: #fff;
  background-color: #653cf4;
}

.hero .buttons .btn-contact .dot {
  color: #fff;
}

.hero .buttons .btn-contact:after {
  background: #0e0d17;
}

.hero .buttons .btn-contact:hover .dot {
  color: #653cf4;
}

.hero .scrolled {
  opacity: .4;
}

@media (width >= 400px) {
  .hero .buttons {
    padding: 8px 18px;
  }
}

@media (width >= 650px) {
  .hero {
    flex-direction: column;
    height: 100%;
    margin-top: 76px;
    margin-bottom: 0;
  }

  .hero .titles {
    text-align: center;
    align-items: center;
    gap: 0;
  }

  .hero .titles h1 {
    letter-spacing: .8px;
    word-spacing: 4px;
    font-size: 16px;
  }

  .hero .titles h2 {
    letter-spacing: -3.36px;
    max-width: 586px;
    margin-top: 6px;
    font-size: 48px;
  }

  .hero .titles h3 {
    letter-spacing: -.28px;
    max-width: 442px;
    margin-top: 12px;
    padding-right: 0;
    font-size: 14px;
  }

  .hero .buttons {
    flex-direction: row-reverse;
    justify-content: center;
    gap: 8px;
    margin-top: 34px;
    padding: 0;
    position: static;
  }

  .hero .buttons .button-fill-hover {
    letter-spacing: -.32px;
    border-radius: 16px;
    gap: 12px;
    padding: 12px 24px;
    font-size: 16px;
  }
}

@media (width >= 900px) {
  .hero .titles h2 {
    letter-spacing: -4.2px;
    max-width: 730px;
    font-size: 60px;
  }

  .hero .titles h3 {
    letter-spacing: -.28px;
    max-width: 640px;
  }

  .hero .buttons {
    margin-top: 30px;
  }
}

@media (width >= 1200px) {
  .hero .titles h2 {
    max-width: 730px;
  }
}
